// Typography and fonts

// Import some fonts
@import url('https://fonts.googleapis.com/css?family=Spectral:700');
@import url('https://fonts.googleapis.com/css2?family=PT+Sans+Caption:wght@700');
@import url('https://rsms.me/inter/inter-ui.css');

// Headers
h1, h2, h3, h4, h5, h6 {
  font-family: 'PT Sans Caption', sans-serif;
  font-weight: bold;
  text-rendering: geometricPrecision; // or optimizeLegibility
  color: #2a3a4b;
}
h1 { font-size: 3rem; }
h2 { font-size: 2.5rem; }
h3 { font-size: 2rem; }
h4 { font-size: 1.5rem; }
h5, h6 { font-size: 1rem; }

// Paragraph
p { font-family: 'Inter UI', sans-serif; }

// Links
a {
  color: inherit;
  text-decoration: underline;
  // `:focus` is linked to `:hover` for basic accessibility
  // &:hover, &:focus {
    // text-decoration: none;
    // color: #4b2a3a;
    // background-color: #45607c;
  // }
  strong {
    color: inherit;
  }
}

// Text highlight color
::-moz-selection { 
  background-color: #2a3a4b; 
  color: #fff;
}
::selection {
  background-color: #2a3a4b;
  color: #fff;
}
